.fotter{
    background-color: rgb(193 0 0);
    padding: 40px;
    margin-top: 20px;
    color: white;
    

}
.fotter .container{
    display: flex;
    justify-content: center;
    align-items: center;
  
}
.fotter a{
    
    border: 1px solid black;
    padding: 10px 10px;
    font-size: 45px;
    cursor: pointer;
    color: black;
    background-color: white;
    
}
.fotter i{
    
    
    padding-right:8px ;
    font-size: 45px;
    
}
@media (max-width:500px) {
    .fotter{
   
        
        margin-top: 150px;
        
    
    }
}